.container {
    margin-bottom: 10px;
}

.link {
    display: inline-flex;
    align-items: center;
    padding: 3px 10px;
    background-color: #dddddd;
    font-size: 14px;
    color: #515151;

    border-radius: 5px;
    border: 1px solid #bbbbbb;

    &:hover,
    :focus {
        color: #dddddd;
        background-color: #46d5aa;
    }
}